/* BOOK ISHH  */
/* ----------------------------------------------------------------------- */

.book,
.book img,
.book::after,
.book::before {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

ul {
  display: table-cell;
  vertical-align: middle;
}

.list-inline {
  margin: 10%;
  list-style: none;
}

.list-inline > li {
  display: inline-block;
  margin-left: 2em;
}

.list-inline > .book:first-child {
  margin-left: 450px;
  margin-top: 200px;
}

.book {
  cursor: pointer;
  display: block;
  width: 252px;
  height: 350px;
  position: relative;
  background: white;
  z-index: 1;
  /* box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 9px 20px 0 rgba(0, 0, 0, 0.25); */
  /* box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2); */
  box-shadow: 10px 20px 50px 1px rgba(109, 141, 173, 0.25),
    -10px 0px 50px 1px rgba(109, 141, 173, 0.25);
  overflow: hidden;
  transition: box-shadow 0.3s linear;
}

.book img {
  width: inherit;
  height: inherit;
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transform: rotateY(0);
  transform: rotateY(0);
  transition: all 0.45s ease;
}

/* .book:hover {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25), 0 9px 20px 0 rgba(0, 0, 0, 0.45);
} */

.book:hover img {
  -webkit-transform: rotateY(-25deg);
  transform: rotateY(-25deg);
  box-shadow: 1px 1px 5px 5px rgba(0, 0, 0, 0.2);
}

.book::after,
.book::before {
  content: "";
  display: block;
  width: inherit;
  height: inherit;
  position: absolute;
  z-index: -1;
  top: 0;
  background: white;
  border: 1px solid #d9d9d9;
}

.book::before {
  left: -3px;
}

.book::after {
  left: -6px;
}

