    bottom: 0;
}

svg#curve {
    position: absolute;
    fill: transparent;
    left: 0;
    bottom: 0;
    width: 400px;
    height: 450px;
}

.connections {
    height: 80px;
    width: 400px;
    position: absolute;
    bottom: 50px;
    margin-left: 20px;
    margin-top: 20px;
}

.connection {
    height: 25px;
    width: 25px;
    border-radius: 100%;
    background-color: white;
    display: inline-block;
    margin-right: 25px;
    -webkit-transform: translateY(200px);
            transform: translateY(200px);
    
    transition: -webkit-transform 1s cubic-bezier(.46, 1.48, .18, .81);
    
    transition: transform 1s cubic-bezier(.46, 1.48, .18, .81);
    
    transition: transform 1s cubic-bezier(.46, 1.48, .18, .81), -webkit-transform 1s cubic-bezier(.46, 1.48, .18, .81);
    
}

.connection.twitter{
    transition-delay:  0.06s;
}
.connection.behance{
    transition-delay: 0.12s;
}
.dev-card:hover .connection {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

.info {
	font-family: Inconsolata;
    padding-left: 20px;
    -webkit-transform: translateY(250px);
            transform: translateY(250px);
    color: white;
    transition: -webkit-transform 1s cubic-bezier(.31,1.21,.64,1.02);
    transition: transform 1s cubic-bezier(.31,1.21,.64,1.02);
    transition: transform 1s cubic-bezier(.31,1.21,.64,1.02), -webkit-transform 1s cubic-bezier(.31,1.21,.64,1.02);
}

.dev-card:hover .info {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

.name {
    font-weight: bolder;
    padding-top: 5px;
}

.job {
    margin-top: 10px;
}


.connection .icon{
    display: flex;
    justify-content: center;
    align-items: center;

}
.dev-card {
    background-size: cover;
    max-width:100%;
    max-height:100%;
}

.twitter{
    padding-top: 2px;
}
