@-webkit-keyframes oppai {
    0%{
        opacity: 0;
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@keyframes oppai {
    0%{
        opacity: 0;
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

