.content-card {
  background-color: #ffffff;
  border-radius: 13px;
  box-shadow: 4px 24px 60px 1px rgba(109, 141, 173, 0.25);
}

.text-wrapper {
  text-align: center;
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: bold;
}

.video-wrapper {
  height: 500px;
  width: 800px;
  padding: 10px;
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 920px) {
  .video-wrapper {
    height: 250px;
    width: 100%;
  }
}
